import React from "react";
import qs from "qs";
import { path, isEmpty, isNil } from "ramda";
import stringify from "json-stringify-safe";

import {
  lifecycle,
  compose,
  withStateHandlers,
  branch,
  renderComponent,
} from "recompose";

import LearningCenter from "components/learningCenter/learningCenterTemplate";

import { getPreviewClient } from "../utils";

import { MessageWrapper } from "./elements";

const ContentfulPreview = ({ pageData, location }) => (
  <LearningCenter
    pageContext={pageData}
    location={location}
    indexPage={false}
  />
);

const _lifecycle = {
  async componentDidMount() {
    const landingPageSpaceClient = getPreviewClient(
      process.env.CONTENTFUL_LEARNING_CENTER_ACCESS_TOKEN_PREVIEW,
      process.env.CONTENTFUL_LEARNING_CENTER_SPACE_ID,
    );

    try {
      const urlSearch = qs.parse(window.location.search, {
        ignoreQueryPrefix: true,
      });

      const category = path(["category"], urlSearch);
      const url = path(["url"], urlSearch);

      if (isNil(url)) {
        throw Error(
          "Please specify a URL to test via a URL parameter (i.e. www.brex.com/contentful-preview/learn?category=loans-credit-scores&url=improve-business-credit-score)",
        );
      }

      const entries = await landingPageSpaceClient.getEntries({
        content_type: "articleHeader",
        "fields.category": category,
        "fields.urlPath": url,
        include: 4,
      });

      if (isEmpty(entries.items)) {
        throw Error(
          "The URL path that was provide was not matched to an learn article. Please try again with a different path.",
        );
      }
      // this library json-stringify-safe takes all data returned from Contentful, stringifies it and removes all circular references, which you then have to parse as an object. https://github.com/contentful/contentful.js/issues/249#issuecomment-403237882
      this.props.setPageData({
        contentfulData: stringify(entries.items[0].fields, null, 1),
      });
    } catch (error) {
      this.props.setError(error.message);
    }
  },
};

export default compose(
  withStateHandlers(
    {
      pageData: null,
      error: null,
    },
    {
      setPageData: () => (data) => ({ pageData: data }),
      setError: () => (errorMessage) => ({ error: errorMessage }),
    },
  ),
  lifecycle(_lifecycle),
  branch(
    ({ error }) => !!error,
    renderComponent(({ error }) => <MessageWrapper>{error}</MessageWrapper>),
  ),
  branch(
    ({ pageData }) => !pageData,
    renderComponent(() => (
      <MessageWrapper>Fetching contentful data...</MessageWrapper>
    )),
  ),
)(ContentfulPreview);
